.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  position: relative;
  min-height: 3rem;
  padding: 0.5rem 1rem;
  color: #6f7281;
  background-color: #fafafa;
  border: 2px solid #c4c9cb;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: all 150ms cubic-bezier(0.65, 0, 0.35, 1);
}
.button--hover, .button:hover, .button:focus {
  color: #6b52c7;
  border-color: #6b52c7;
}
.button--hover, .button:hover {
  color: #35247c;
