.form-group {
  display: grid;
  grid-template-rows: auto 3rem auto;
  grid-template-columns: 3rem 1fr 3rem;
  grid-template-areas: "label      label       label" "left-icon  input       right-icon" "help-text  help-text   help-text";
  align-content: start;
  position: relative;
  margin-bottom: 1.5rem;
