}
.form-group--lg {
  width: 24ch;
}
.form-group--xl {
  width: 32ch;
}
.form-group--xxl {
  width: 40ch;
}

.form-group__label {
  grid-area: label;
}

.form-group__input {
  grid-area: left-icon/left-icon/right-icon/right-icon;
  appearance: none;
  display: block;
  max-width: 100%;
  padding: 0 0.5rem;
  line-height: 3rem;
  color: #6f7281;
  background-color: #fafafa;
  border: 2px solid #c4c9cb;
  border-radius: 0.25rem;
