  outline: none;
}
.form-group__input.disabled, .form-group__input[disabled] {
  color: #9aa2a9 !important;
  background: #fafafa !important;
  border: 2px solid #c4c9cb !important;
  cursor: not-allowed !important;
  box-shadow: none !important;
  opacity: 0.5;
}
.form-group__input.disabled::placeholder, .form-group__input[disabled]::placeholder {
  color: #363639;
  opacity: 0.5;
}
.form-group__input.disabled:hover, .form-group__input[disabled]:hover {
