.form-group--checkbox,
.form-group--radio {
  grid-template-rows: auto 3rem;
  grid-template-columns: auto;
  grid-auto-rows: minmax(3rem, auto);
  grid-template-areas: "label" "inputs";
  grid-row-gap: 0.5rem;
  justify-content: start;
}

.form-group--checkbox .form-group__input,
.form-group--radio .form-group__input {
  grid-area: auto;
  display: grid;
  grid-template-columns: 3rem auto;
  grid-template-areas: "input label";
  align-items: center;
  padding: 0.5rem 1rem 0.5rem 0;
  text-transform: none;
  letter-spacing: normal;
}

.form-group--checkbox .form-group__input:focus-within,
.form-group--radio .form-group__input:focus-within {
