  margin-bottom: -0.5rem;
}

.form-group--checkbox .form-group__radio,
.form-group--radio .form-group__radio {
  margin: 0;
}

.form-group--checkbox .form-group__radio:focus,
.form-group--radio .form-group__radio:focus {
  outline: none;
}

