  grid-template-columns: auto;
  grid-template-areas: "label" "input" "help-text";
}

.form-group--file .form-group__input {
  grid-area: input;
  display: grid;
  grid-template-rows: 1.5rem 6rem 1.5rem 0.75rem;
  grid-template-areas: "." "icon" "text" ".";
  justify-items: center;
  align-items: center;
  color: #9aa2a9;
}

.form-group__file {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  opacity: 0;
}

.form-group__file-icon {
  grid-area: icon;
