  content: "";
  position: relative;
  border-style: solid;
  border-width: 0.4rem 0.25rem;
  border-radius: 2px;
  pointer-events: none;
  transition: border-color 150ms cubic-bezier(0.65, 0, 0.35, 1);
}

.form-group--select::before {
  top: 0.5rem;
  border-color: #9aa2a9 transparent transparent transparent;
}

.form-group--select::after {
  top: -0.5rem;
  border-color: transparent transparent #9aa2a9 transparent;
}

.form-group--select .form-group__input {
  padding-right: 3rem;
}

