body {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 2rem;
  grid-template-areas: "header" "main" "footer";
  min-height: 100vh;
}

header {
  grid-area: header;
}

main {
  grid-area: main;
  justify-self: center;
  max-width: 50em;
}

footer {
  grid-area: footer;
}

nav {
  position: sticky;
  top: 1rem;
}

footer {
  height: 3rem;
  background-color: #363639;
  color: #9aa2a9;
  text-align: center;
}

.grid {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: 1fr;
  grid-gap: 1rem;
}

.grid-columns {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0 1rem;
  grid-auto-flow: dense;
}

.grid-columns-half {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 0 1rem;
}

.grid-columns-5 {
  display: grid;
  grid-template-columns: repeat(5, auto);
  grid-gap: 1rem;
  justify-items: start;
  justify-content: start;
  margin-bottom: 1rem;
}

